<template>
    <div class="main-container-inner">
        <div class="main-wrapper">
            <div class="title-wrapper flex-item">
            </div>
            <div class="content-wrapper flex-item">
                <div
                    v-show="isCoverShow"
                    class="cover-holder"
                    @click="toggleArticle('kon-eternal')"
                    >
                    <div class="cover-text-holder">
                        极东<br />乐园<br />笔记
                    </div>
                </div>
                <transition name="el-fade-in">
                    <div v-if="isRoutingShowing">
                        <router-view />
                    </div>
                </transition>
            </div>
            <div class="title-wrapper flex-item with-content">
                <div
                    v-for="(item, index) in article"
                    :key="index"
                    class="title-holder"
                    :class="{'showing-title': index == showingIndex}"
                    @click="toggleArticle(item.route)"
                    >
                    <p 
                        class="title-text" 
                        >
                        <i v-show="showingIndex == index" class="fa fa-dot-circle-o list-icon"></i>
                        <i v-show="showingIndex !== index" class="fa fa-circle-o list-icon"></i>
                        {{item.title}}
                    </p>
                    <p class="intro-text">{{item.intro}}</p>
                </div>
                <div
                    v-show="!isCoverShow"
                    class="title-holder"
                    @click="toggleArticle()"
                    >
                    <span class="switch-text">
                        合上书
                        <i class="fa fa-book"></i>
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { articleData } from '@/components/tearoom/article.js'
export default {
    data() {
        return {
            visibleController: false,
            statusController: null,
            articleInShow: null,
            article: articleData,
            isRoutingShowing: true,
        }
    },
    computed: {
        currentName() {
            return this.$route.params.name;
        },
        showingIndex () {
            const name = this.currentName;
            if (!name) return -1;
            return this.article.map(item => item.route).indexOf(name);
        },
        isCoverShow() {
            return !Boolean(this.currentName);
        },
    },
    methods: {
        toggleArticle(route) {
          if (!route)  {
            this.$router.push('/');
            return;
          }
          if (route === this.currentName) {
              return;
          }
          this.isRoutingShowing = false;
          this.$router.push(`/post/${route}`)
          .then(() => {
              setTimeout(() => {this.isRoutingShowing = true}), 1000}
          )
          .catch(err => {
              console.log(err);
          })
        }
    }
}
</script>

