<template>
  <div class="single-content">
    <p class="paragraph">
      <img 
        class="article-img" 
        v-if="classChecker === 'image'" 
        :src="line.substr(1)"
      />
      <span v-else-if="classChecker === 'normal'">
        {{line}}
      </span>
      <span v-else-if="classChecker === 'special'" class="special">
        {{line.substr(1)}}
      </span>
      <img v-else-if="classChecker === 'avatar'" 
        class="avatar"
        :src="line.substr(1)"
      />
      <span v-else-if="classChecker === 'dialog-left'" class="dialog-left">
        {{line.substr(1)}}
      </span>
      <span v-else-if="classChecker === 'quote'" class="quote">
        {{line.substr(1)}}
      </span>
    </p>
  </div>
</template>

<script>
export default {
  name: "ContentRender",
  props: {
    line: {
      type: String,
      default: 'qweqweqwe',
    },
  },
  computed: {
    classChecker() {
      const identify = this.line[0];
      switch (identify) {
        case '~': {
            return 'quote';
        }
        case '@': {
            return 'special';
        }
        case '!': {
            return 'image';
        }
        case '%': {
          return 'avatar'
        }
        case '^': {
          return 'dialog-left'
        }
        default: {
            return 'normal';
        }
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.paragraph {
        line-height: 20px;
        font-size: 16px;
        margin-bottom: 12px;
        font-family: 'Noto Serif SC', serif;
        color: #303133;
    }
    .quote {
        display: block;
        font-size: 16px;
        color: #909399;
        border-left: 2px solid #909399;
        padding: 10px 10px;
        font-family: 'Noto Serif SC', serif;
        line-height: 22px;
    }
    .special {
        font-style: italic;
        font-weight: bold;
    }
    .article-img {
        width: 80%;
        margin: auto;
        display: block;
        margin-top: 15px;
        margin-bottom: 15px;
    }
    .avatar {
      display: block;
      border-radius: 50%;
      width: 60px;
      height: 60px;
      object-fit: cover;
      object-position: center;
      overflow: hidden;
      &::after {
        content: ':'
      }
    }
    .dialog-left {
      padding-left: 40px;
      color: #b96f6f;
      font-style: italic;
    }
    .paragraph::first-letter {
        padding-left: 20px;
    }
</style>